@use "../global/" as *;

.p-all {
  margin-top: rem(40);
  overflow-x: auto;

  &__table {
    width: 100%;
    border-collapse: separate;
    border-spacing: rem(5) 0;
  }

  &__header {
    padding: rem(12) rem(15);
    text-align: center;
    border-top-left-radius: rem(10);
    border-top-right-radius: rem(10);
    background: #284563;
    border-bottom: 1px solid rgba($color: #fafafa, $alpha: 0.3);
    position: relative;
    border-top-left-radius: rem(10);
    border-top-right-radius: rem(10);
    &--empty {
      background-color: transparent;
    }
  }

  &__header--empty {
    background: transparent;
    mix-blend-mode: normal;
  }
  &__header p {
    z-index: 2;
    position: relative;
    font-weight: 700;
    font-size: rem(20);
    letter-spacing: 0.1em;
    line-height: 2;
    color: #fafafa;
    white-space: nowrap;
    @include mq(md) {
      font-size: clamp(14px, 2vw, 14px);
    }
    @include mq(lg) {
      font-size: rem(20);
    }
  }

  &__label {
    color: #333;
    padding: rem(20) rem(25);
    font-size: rem(18);
    font-weight: 700;
    text-align: center;
    position: relative;
    letter-spacing: 0.1em;
    background: #f2f1ec;
    white-space: nowrap;
    z-index: 2;
    width: rem(200);
    border-bottom: 1px solid #c7c1b0;
    @include mq(md) {
      width: rem(320);
      font-size: clamp(14px, 2vw, 14px);
    }
    @include mq(lg) {
      font-size: rem(18);
    }

    &--first {
      border-top-left-radius: rem(10);
    }
    &--last {
      border-bottom-left-radius: rem(10);
      border-bottom: initial;
    }
  }

  &__cell {
    padding: rem(20);
    text-align: center;
    background: #284563;
    position: relative;
    border-bottom: 1px solid rgba($color: #fafafa, $alpha: 0.3);
  }

  &__cell p {
    z-index: 2;
    position: relative;
    color: #fafafa;
    font-weight: 400;
    font-size: rem(18);
    letter-spacing: 0.1em;
    line-height: 2;
    @include mq(md) {
      font-size: clamp(14px, 2vw, 14px);
    }
    @include mq(lg) {
      font-size: rem(18);
    }
  }

  // 
  @media screen and (max-width: 768px) {
    margin-top: rem(40);

    &__table {
      display: block;
      overflow-x: auto;
      -webkit-overflow-scrolling: touch;
    }

    &__header {
      font-size: rem(20);
      padding: rem(12) rem(15);
      text-align: center;
      font-weight: 700;
      letter-spacing: 0.1em;
      line-height: 2;
      color: #fafafa;
    }

    &__label {
      font-size: rem(14);
      text-align: center;
      padding: rem(15) rem(18);
      @include mq(md) {
        font-size: clamp(14px, 2vw, 14px);
      }
      @include mq(lg) {
        font-size: rem(14);
      }
    }

    &__cell {
      font-size: rem(16);
      text-align: center;
      padding: rem(15);
      @include mq(md) {
        font-size: clamp(14px, 2vw, 14px);
      }
      @include mq(lg) {
        font-size: rem(16);
      }
    }
  }
}
.p-all__cell--last {
  border-bottom-right-radius: rem(10);
  border-bottom-left-radius: rem(10);
}
.p-all__header span {
  font-size: rem(14);
  font-weight: 700;
  color: #fafafa;
  letter-spacing: 0.1em;
  white-space: nowrap;
  line-height: calc(30 / 14);
  @include mq(md) {
    font-size: clamp(14px, 2vw, 14px);
  }
  @include mq(lg) {
    font-size: rem(14);
  }
}
.p-fee .p-all__label {
  width: rem(240);
}
.p-all__label--fee {
  background: #fafafa;
  padding-top: rem(18);
  padding-bottom: rem(18);
  color: #051026;
  font-size: rem(14);
  font-weight: 700;
  letter-spacing: 0.1em;
  line-height: calc(36 / 20);
  @include mq(md) {
    font-size: clamp(14px, 2vw, 14px);
  }
  @include mq(lg) {
    font-size: rem(20);
  }
}
.p-all__cell--fee--black {
  background: #051024;
}
.bb0 {
  border-bottom: initial;
}